<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.Type" @input="change" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="users" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<!-- <el-table-column type="index" width="60">
			</el-table-column> -->
			<el-table-column prop="id" label="ID" width="80" sortable>
			</el-table-column>
			<el-table-column label="" prop="Type" width="200" >
			</el-table-column>
			<el-table-column prop="Title" label=""  :show-overflow-tooltip="true"  width="200" >
			</el-table-column>
		<el-table-column prop="Text" label="" :show-overflow-tooltip="true"    width="200" >
		</el-table-column>
			<el-table-column prop="ImgSrc" label="" :show-overflow-tooltip="true"  width="200">
			</el-table-column>
			<el-table-column prop="Time" label=""  width="200" >
			</el-table-column>
			<el-table-column prop="Browse" label=""  width="200" >
			</el-table-column>
			<el-table-column label="" width="150">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
			
			<el-form-item label="" prop="Type">
				<!-- <el-input v-model="editForm.Type" auto-complete="off"></el-input> -->
				<el-select v-model="editForm.Type" auto-complete="off" style="width: 100%;" placeholder="">
				  <el-option v-for="item in newsType"
				             :key="item.value"
				             :label="item.label"
				             :value="item.value"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item prop="Title" label="">
				<el-input v-model="editForm.Title" auto-complete="off"></el-input>
		
			</el-form-item>
			
			          
			           
		<el-form-item prop="Text" label="">
			<el-input v-model="editForm.Text" auto-complete="off"></el-input>
		</el-form-item>
		<el-form-item prop="ImgSrc" label="">
			<el-input v-model="editForm.ImgSrc" auto-complete="off"></el-input>
		</el-form-item>
		<el-form-item prop="Time" label="">
			<el-input v-model="editForm.Time" auto-complete="off"></el-input>
		</el-form-item>
		<el-form-item prop="Browse" label="">
			<el-input v-model="editForm.Browse" auto-complete="off"></el-input>
		</el-form-item>
		
			
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
		
			
				<el-form-item label="" prop="Type">
				<!-- 	<el-input v-model="addForm.Type" auto-complete="off"></el-input> -->
				<el-select v-model="addForm.Type" auto-complete="off" placeholder="">
				  <el-option style="width: 100%;" v-for="item in newsType"
				             :key="item.value"
				             :label="item.label"
				             :value="item.value"></el-option>
				</el-select>
				</el-form-item>
				<el-form-item prop="Title" label="">
					<el-input v-model="addForm.Title" auto-complete="off"></el-input>
			
				</el-form-item>
			<el-form-item prop="Text" label="">
				<el-input v-model="addForm.Text" auto-complete="off"></el-input>
			</el-form-item>
			<el-form-item prop="ImgSrc" label="">
				<el-input v-model="addForm.ImgSrc" auto-complete="off"></el-input>
			</el-form-item>
			<el-form-item prop="Time" label="">
				<el-input v-model="addForm.Time" auto-complete="off"></el-input>
			</el-form-item>
			<el-form-item prop="Browse" label="">
				<el-input v-model="addForm.Browse" auto-complete="off"></el-input>
			</el-form-item>
			
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getnewsList,removenews,addnews,editnews} from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					Type: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					Type: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
				
				},
                 newsType:[
					 {
						 label:'',
						 value:''
					 },
					 {
					 						 label:'',
					 						 value:''
					 },
					 {
					 						 label:'',
					 						 value:''
					 },
					 {
					 						 label:'',
					 						 value:''
					 }
				 ],
				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
				Type: [
					{ required: true, message: '', trigger: 'blur' }
				]
				},
				//
				addForm: {
				
				}

			}
		},
		methods: {
			//
			
			change(){
			
				if(this.filters.Type==""){
					this.getUsers()
				}
			},
		
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//
			getUsers() {
				let para = {
				currentPage:this.page,
					Type: this.filters.Type,
					pagesize:20
				};
				// this.listLoading = true;
				//NProgress.start();
		//console.log(this.filters.name)
				getnewsList(para).then(data => {
					//console.log(data)
					this.users=data.list
					this.total=data.total
						//this.listLoading = false;
						//NProgress.done();
					//NProgress.done();
				});
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					//this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					console.log(para)
					removenews(para).then((res) => {
						console.log(res)
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getUsers();
					});
					//console.log(JSON.parse(sessionStorage.getItem('user')).username )
				}).catch(() => {

				});
			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					// name: '',
					// sex: -1,
					// age: 0,
					// birth: '',
					// addr: ''
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							console.log(para)
							editnews(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								console.log(res)
								if(res.code==200){
									this.$message({
										message: '',
										type: 'success'
									});
									this.$refs['editForm'].resetFields();
									this.editFormVisible = false;
									this.getUsers();
								}else{
									this.$message({
										message: res.message,
										type: 'warning'
									});
								}
							
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							console.log(para)
							// para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							addnews(para).then((res) => {
								console.log(res)
								this.addLoading = false;
									if(res.code==200){
										
										//NProgress.done();
										this.$message({
											message: '',
											type: 'success'
										});
										this.$refs['addForm'].resetFields();
										this.addFormVisible = false;
										
										}else{
											this.$message({
												message: res.message,
												type: 'warning'
											});
											this.$refs['addForm'].resetFields();
											this.addFormVisible = false;
											
										}
							this.getUsers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					//this.listLoading = true;
					//NProgress.start();
					let para = { idd: ids };
				
				var k=para.idd
				k=k.split(',')//id
					
					var flag=false
				
					for(var i=0;i<k.length;i++){
						
						removenews({id:k[i]}).then((res) => {
							//console.log(res)
							if(res.code!=200){
								flag=true
							}
					
						
						});
					}
					if(flag!=true){
						let that=this
						setTimeout (function(){
							that.listLoading = false;
							that.$message({
								message: '',
								type: 'success'
							});
								that.getUsers();
						},200)
					}
			
				}).catch(() => {

				});
			}
		},
		mounted() {
			 this.getUsers();
			
	
		}
	}

</script>

<style scoped>

</style>


